.App {
  text-align: center;
  position: relative;
  overflow: hidden;
}

.title {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  font-family: 'Core Circus', sans-serif;
  font-weight: 800;
  letter-spacing: 0;
  color: transparent;
  background-image: url(https://media2.giphy.com/media/v1.Y2lkPTc5MGI3NjExaTJrNnFyOHZrbGNnM2w5dHUxeGFtZmRraXlnYjBhbWNjbjB5dmluMiZlcD12MV9pbnRlcm5hbF9naWZfYnlfaWQmY3Q9Zw/iYTXFJofI7I987H92k/giphy.gif);
  background-size: cover;
  background-color: transparent;
  -moz-background-clip: text;
  -webkit-background-clip: text;
  font-size: 100px;
  -webkit-text-stroke: 1px #000;
  -webkit-text-stroke-width: 1px;
  width: 100%;
}


@media (max-width: 480px) {
  .title {
    font-size: 70px;
  }
}

