.container1 {
  position: relative;
  font-family: sans-serif;
  margin-left: auto;
  margin-right: auto;
  width: 95%;
  z-index: 1;
}

.container1::before,
.container1::after {
  content: "";
  background-color: #ea8b19;
  position: absolute;
}

.container1 .box {
  width: 100%;
  height: 3200px;
  background-color: #ffffff13;
  border: 2px solid rgba(255, 255, 255, 0.222);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  border-radius: 0.7rem;
  transition: all ease 0.3s;
  z-index: 2;
  box-shadow: 0px 0px 20px 10px #ea8b19;
}

.container1 .box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container1 .box div strong {
  display: block;
}

h2 {
  font-family: 'Core Circus', sans-serif;
  font-size: 130px;
  text-align: center;
  position: absolute;
  color: rgba(45, 45, 45, 1);
  letter-spacing: -0.5rem;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 2em;
  z-index: 3;

  @media (max-width: 480px) {
    font-size: 90px;
  }
}

h2:before {
  content: "!";
  position: absolute;
  overflow: hidden;
  color: #ffcc00;
  width: 100%;
  z-index: 4;
  text-shadow: none;
  left: 0%;
  text-align: right;
  animation: flicker 3s linear infinite;

  @media (max-width: 480px) {
    left: 0%;
    text-align: right;
    top:1000px
  }
}

@keyframes flicker {
  0%,
  19.999%,
  22%,
  62.999%,
  64%,
  64.999%,
  70%,
  100% {
    opacity: 0.99;
    text-shadow: -1px -1px 0 rgba(255, 255, 255, 0.4),
      1px -1px 0 rgba(255, 255, 255, 0.4),
      -1px 1px 0 rgba(255, 255, 255, 0.4),
      1px 1px 0 rgba(255, 255, 255, 0.4), 0 -2px 8px, 0 0 2px,
      0 0 5px #ff7e00, 0 0 15px #ff4444, 0 0 2px #ff7e00,
      0 2px 3px #000;
  }
  20%,
  21.999%,
  63%,
  63.999%,
  65%,
  69.999% {
    opacity: 0.4;
    text-shadow: none;
  }
}



