
  
  .wordEffectContainer {
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgba(45, 45, 45, 1);
    font-size: 130px;
    text-align: center;
    line-height: 1;
    margin: 0;
    letter-spacing: 1rem;
    
    
  }
  
  @media (max-width: 480px) {
    .wordEffectContainer {
      margin-top: -800px;/* Adjust the margin-top value as needed */
      font-size: 90px;
      text-align: center;
    }
  }
  
  h2 {
    font-family: 'Core Circus', sans-serif;
    font-size: 130px;
    text-align: center;
    position: absolute;
    color: rgba(45, 45, 45, 1);
    letter-spacing: -0.5rem;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: 2em;
    z-index: 3;
  }
  
  h2:before {
    content: '!';
    position: absolute;
    overflow: hidden;
    color: #ffcc00;
    width: 100%;
    z-index: 4;
    text-shadow: none;
    left: 0%;  
    text-align: right;
    animation: flicker 3s linear infinite;
  }
  
  @media (max-width: 480px) {
    h2 {
      font-size: 90px;
      transform: translate(-50%, -50%);
      left: 50%; 
      top: 50%; 
    }
  
    h2:before {
      left: 0%;
      text-align: right;
      margin-top: -800x;/* Adjust the margin-top value as needed */
    }
  }
  
  @keyframes flicker {
    0%,
    19.999%,
    22%,
    62.999%,
    64%,
    64.999%,
    70%,
    100% {
      opacity: 0.99;
      text-shadow: -1px -1px 0 rgba(255, 255, 255, 0.4),
        1px -1px 0 rgba(255, 255, 255, 0.4),
        -1px 1px 0 rgba(255, 255, 255, 0.4),
        1px 1px 0 rgba(255, 255, 255, 0.4), 0 -2px 8px, 0 0 2px,
        0 0 5px #ff7e00, 0 0 15px #ff4444, 0 0 2px #ff7e00,
        0 2px 3px #000;
    }
    20%,
    21.999%,
    63%,
    63.999%,
    65%,
    69.999% {
      opacity: 0.4;
      text-shadow: none;
    }
  }
  